  box-sizing: border-box;
  width: 100px;
  color: gray;
  font-size: 15px;
  text-align: center;
  float: right;
  margin-top: 10px;
}
.partition {
  border-bottom: 1px solid gray;
  padding: 0 50px;
  font-size: 20px;
  line-height: 30px;
}
.partition-icon {
  height: 50px;
  float: left;
  margin-right: 20px;
}
.partition>h3 {
  position: relative;
  top: 10px;
}
.sub-partitions {
  background-color: #82beec;
  border-color: transparent;
  width: 100px;
  padding: 10px 0;
  margin: 10px 15px;
}
.sub-partitions:hover{
  background-color: #b3d8ff;
}
.sub-partitions:first-child:hover{
  background-color: #82beec;
  cursor: default;
}
#sub-container {
  display: inline-block;
  position: relative;
  top: 10px;
  width: 75%;
  margin-bottom: 30px;
  font-size: 0px;
}
.follow-partition {
  float: right;
  margin: 0;
}
</style>



